

.services-main-container {
  width: 100%;
  height: 100%;
  background-color: #161616;
  display: flex;
  flex-direction: column;
}

.services-main-background {
  position: relative;
  top: 0;
  height: 200vh;
  width: 100%;
  background-image: url('./images/services-bckgrnd.jpg');
  background-size: contain;
  background-repeat: no-repeat;
}

.services-main-background-gradient {
  position: relative;
  height: 35vh;
  width: 100%;
  background-color: #161616;
}

.services-main-content-container {
  position: absolute;
  top: 24%;
  //height: 170%;
  width: 90%;
  left: 50%;
  transform: translate(-50%);
  background-color: transparent;
  z-index: 2;
  display: flex;
  flex-direction: column;
}

.services-main-title {
  position: relative;
  height: 196px;
  width: 100%;
  color: white;
  text-transform: uppercase;
  font-size: 41px;
  letter-spacing: 3px;
  display: flex;
  justify-content: center;
}

.services-content-top {
  position: relative;
  height: 500px;
  width: 100%;
  background-color: #161616;
  opacity: 0.7;
  display: flex;
  flex-direction: row;

}

.services-top-services {
  position: relative;
  height: 100%;
  width: calc(100% / 5);
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  color: #cccccc;
  text-transform: uppercase;
  letter-spacing: 3px;
}
.services-top-services p {
  position: absolute;
  bottom: 90px;
}

.services-tile {
  height: 75%;
  width: 75%;
}
#services-tile-four {
  height: 55%;
  width: 55%;
  margin-top: 50px;
}
.down-arrow {
height: 15px;
  position: absolute;
  bottom: 30px;
}

.services-content-middle {
  position: relative;
  height: 420px;
  width: 100%;
  background-color: white;
}
.content-two-top {
  position: absolute;
  top: 0;
  width: 100%;
  height: 15px;
  background-color: #cccccc;
  display: flex;
  flex-direction: row;
}
.top-two-sec {
  position: relative;
  height: 100%;
  width: calc(100% / 5);
}
.services-content-title {
  position: absolute;
  top: 30%;
  left: 10%;
  text-transform: uppercase;
  color: #BD9A35;
  letter-spacing: 3px;
  font-size: x-large;
  font-weight: bold;
}
.service-content-info {
  position: absolute;
  top: 27%;
  left: 30%;
  color: #161616;
  height: 1000px;
  width: 50%;
  letter-spacing: 2px;
  font-size: medium;
  line-height: 40px;
}

.services-accordion {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.services-accordion-section {
  position: relative;
  height: 57px;
  width: 100%;
  background-color: #161616;
  color: #cccccc;
  opacity: 0.7;
  border-top: 1px solid #727272;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.services-accordion-section p {
  position: relative;
  right: -40px;
  font-size: medium;
}
.services-plus-sign {
  position: absolute;
  right: 1px;
  height: 55px;
  width: 40px;
  padding-bottom: 10px;
  cursor: pointer;
}
.services-accordion-pop {
  position: relative;
  height: 0;
  width: 100%;
  background-color: white;
}


.services-looking-for-help {
  margin-top: 100px;
  margin-bottom: 100px;
  position: relative;
}