nav {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100px;
  z-index: 10;
  display: flex;
  align-items: center;

  .nav-logo {
    position: absolute;
    top: 25px;
    left: 35px;
    cursor: pointer;
  }

  .nav-logo-svg {
    height: 50px;
    width: 194px;
  }

  .nav-items  {
    position: absolute;
    top: 50%;
    right: 14%;
    transform: translate(0, -43%);
    height: 100%;
    width: 46%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
  }
  .nav-item-title {
    color: #95989a;
    font-family: 'Open Sans', sans-serif;
    text-transform: uppercase;
    cursor: pointer;
    letter-spacing: 1px;
    transition: 0.25s;
  }
    .nav-item-title p:after {
      content: '';
      display: block;
      margin-top: 7px;
      opacity: 0;
      height: 3px;
      background: #299bde;
      transition: opacity 0.2s;
    }
  .nav-item-title:hover {
    color: white;
  }
  .nav-item-title p:hover:after {
    opacity: 1;
  }

  .nav-item-drop {
    position: absolute;
    top: 63px;
    height: 0;
    width: 200px;
    background-color: transparent;
    display: none;
    flex-direction: column;
  }

  .nav-item-drop-one {

  }
  .nav-item-drop-two {

  }
  .nav-item-drop-three {

  }

  .drop-down-item {
    height: 50px;
    width: 100%;
    background-color: white;
    opacity: 0.7;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #242424;
    margin-top: 3px;
  }

  .cta-button {
    color: white;
    text-transform: none;
    font-weight: 800;
    background-color: #299bde;
    border-radius: 50px;
    padding: 7px 25px;
  }
  .cta-button-nav {
    outline: none;
    border: none;
    position: absolute;
    top: 26px;
    right: 50px;
    cursor: pointer;
    height: 49px;
    width: 184px;
    font-size: large;
  }
}

